.sub{
  // min-height: calc(100vh - 68px);
}
.sub_visual{
  display: block;
  position: relative;
  height: 500px;
  background-size: cover;
  background-position: center;
  &::before{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0,0,0,.5);
    content: '';
  }
}
.area_poster{
  display: flex;
}
.thumb_sub{
  width: 100px;
}
.info_visual{
  flex-grow: 1;
  min-height: 150px;
  padding: 10px;
  box-sizing: border-box;
  color: #fff;
  background-color: rgba(0, 0, 0, .4);
  span{
    display: block;
    font-size: 12px;
    line-height: 20px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .tit_thumb{
    display: block;
    font-size: 14px;    
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .tit_original{
    margin-top: 2px;
    font-size: 12px;
    color: #999;
  }
  .date{
    margin-top: 7px;
    color: cyan;
  }
}

.sub_cont{
  position: relative;
  margin-top: -168px;
  padding: 0 18px 80px;
  .copy{
    display: block;
    margin-top: 35px;
  }
  .overview{
    margin-top: 20px;
    font-size: 13px;
    line-height: 23px;
  }
}  

  
  
  
  
  

